<!--  -->
<template>
  <div
    id="ropute-plan"
    class="navigator-card"
    style="position: absolute; display: none; left: 75%; top: 1%; z-index: 100"
  >
    <el-card class="box-card" shadow="always">
      <div slot="header" class="clearfix">
        <span>目的地导航</span>
        <el-button
          style="float: right; padding: 3px 0"
          type="text"
          @click="onClose"
          >关闭</el-button
        >
      </div>
      <div class="text item">
        <el-row>
          <el-autocomplete
            class="inline-input"
            v-model="navigatorForm.origin"
            :fetch-suggestions="querySearch"
            :placeholder="originText"
            @select="handleSelect"
          ></el-autocomplete>
          <el-autocomplete
            class="inline-input"
            v-model="navigatorForm.target"
            :fetch-suggestions="querySearch"
            :placeholder="targetText"
            @select="handleSelect"
          ></el-autocomplete>
        </el-row>
        <el-row style="margin-top: 15px">
          <el-select
            v-model="PathOptions.value"
            :disabled="PathOptions.disable"
            placeholder="无可用规划"
            style="width: 202px"
            @change="pathChange"
          >
            <el-option
              v-for="item in PathOptions.options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-row>
        <el-row style="margin-top: 25px">
          <el-button type="primary" @click="onSubmit">立即导航</el-button>
        </el-row>
      </div>
    </el-card>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

import axios from "axios";

axios.defaults.baseURL = "http://172.27.145.91:9011";

let pointer = null;

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      originText: "请指定出发点",
      targetText: "请指定目的地",

      selectPathList: [],

      originText: "请指定出发点",
      targetText: "请指定目的地",

      navigatorForm: {
        origin: "",
        target: "",
      },

      PathOptions: {
        options: [
          { label: "步行", value: "walk" },
          { label: "自驾-不停车", value: "noparking" },
          { label: "自驾-停车-室内", value: "inside" },
          { label: "自驾-停车-室外", value: "outside" },
        ],
        value: "",
        disable: true,
      },

      resultFromServer: null,
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    init() {
      pointer = this;
    },

    // 实现联想输入
    querySearch(queryString, cb) {
      var list = this.selectPathList;
      var results = queryString
        ? list.filter(this.createFilter(queryString))
        : list;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return (list) => {
        return (
          list.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    loadAll() {
      return this.$store.state.placeList;
    },
    handleSelect(item) {
      console.log(item);
    },

    // 清空当前组件的所有数据
    refresh() {
      this.navigatorForm.origin = "";
      this.navigatorForm.target = "";

      // 获取定位，并自动填充到出发点输入框
      if (this.$store.state.auto_orient) {
        this.originText = "正在定位...";
        this.getLocation();
      } else {
        this.originText = "请指定出发点";
      }
    },

    getLocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (data) => {
            // todo 获取到定位信息后实现
            console.log(data);
          },
          (error) => {
            console.log(error);
            // 定位信息获取失败
            pointer.origin = "定位失败";
          },
          {
            enableHighAccuracy: true,
            timeout: 3000,
          }
        );
      } else {
        this.$confirm(
          "请在浏览器设置中开放定位权限，或者在设置中关闭'自动定位我的位置'选项",
          "未能获取定位权限",
          {
            confirmButtonText: "好的",
            type: "error",
            center: true,
          }
        );
      }
    },

    onShow() {
      this.refresh();
      document.getElementById("ropute-plan").style.display = "block";
    },

    onSubmit() {
      if (this.navigatorForm.origin != "" && this.navigatorForm.target != "") {
        // todo
        this.AxiosRequest({
          origin: this.navigatorForm.origin,
          target: this.navigatorForm.target,
        });
      } else {
        this.$notify({
          title: "警告",
          message: "必须填写起始点与目标点",
          type: "warning",
        });
      }
    },

    onClose() {
      this.PathOptions.disable = false;
      document.getElementById("ropute-plan").style.display = "none";
      this.$emit('close');
    },

    pathChange(val) {
      switch (val) {
        case "walk":
          this.$emit("draw", {
            line: {
              name: val,
              size: 2,
              color: "green",
              interval: 0.6,
              trailLength: 1.5,
              duration: 1.5,
              data: [{ path: pointer.resultFromServer.walk.path }],
            },
            point: {
              name: "points",
              size: 20,
              data: [
                {
                  id: "001",
                  name: "start",
                  lon: this.resultFromServer.origin[0],
                  lat: this.resultFromServer.origin[1],
                },
                {
                  id: "002",
                  name: "stop",
                  lon: this.resultFromServer.target[0],
                  lat: this.resultFromServer.target[1],
                },
              ],
            },
          });
          break;
        case "noparking":
          this.$emit("draw", {
            line: {
              name: val,
              size: 2,
              color: "blue",
              interval: 0.6,
              trailLength: 1.5,
              duration: 1.5,
              data: [{ path: pointer.resultFromServer.noparking.path }],
            },
            point: {
              name: "points",
              size: 20,
              data: [
                {
                  id: "001",
                  name: "start",
                  lon: this.resultFromServer.origin[0],
                  lat: this.resultFromServer.origin[1],
                },
                {
                  id: "002",
                  name: "stop",
                  lon: this.resultFromServer.target[0],
                  lat: this.resultFromServer.target[1],
                },
              ],
            },
          });
          break;
        case "inside":
          this.$emit("draw", {
            line: {
              name: "drive",
              one: {
                name: val + "car",
                size: 2,
                color: "blue",
                interval: 0.6,
                trailLength: 1.5,
                duration: 1.5,
                data: [{ path: pointer.resultFromServer.inside.carpath }],
              },
              two: {
                name: val + "walk",
                size: 2,
                color: "green",
                interval: 0.6,
                trailLength: 1.5,
                duration: 1.5,
                data: [{ path: pointer.resultFromServer.inside.walkpath }],
              },
            },
            point: {
              name: "points",
              size: 20,
              data: [
                {
                  id: "001",
                  name: "start",
                  lon: this.resultFromServer.origin[0],
                  lat: this.resultFromServer.origin[1],
                },
                {
                  id: "002",
                  name: "stop",
                  lon: this.resultFromServer.target[0],
                  lat: this.resultFromServer.target[1],
                },
              ],
            },
            polygon: {
              name: pointer.PathOptions.value,
              color: "#B1DDF0",
              size: 2,
              data: [{ path: pointer.resultFromServer.inside.polygon }],
            },
          });
          break;
        case "outside":
          this.$emit("draw", {
            line: {
              name: "drive",
              one: {
                name: val + "car",
                size: 2,
                color: "blue",
                interval: 0.6,
                trailLength: 1.5,
                duration: 1.5,
                data: [{ path: pointer.resultFromServer.outside.carpath }],
              },
              two: {
                name: val + "walk",
                size: 2,
                color: "green",
                interval: 0.6,
                trailLength: 1.5,
                duration: 1.5,
                data: [{ path: pointer.resultFromServer.outside.walkpath }],
              },
            },
            point: {
              name: "points",
              size: 20,
              data: [
                {
                  id: "001",
                  name: "start",
                  lon: this.resultFromServer.origin[0],
                  lat: this.resultFromServer.origin[1],
                },
                {
                  id: "002",
                  name: "stop",
                  lon: this.resultFromServer.target[0],
                  lat: this.resultFromServer.target[1],
                },
              ],
            },
          });
          break;
        default:
          break;
      }
    },

    AxiosRequest(data) {
      console.log(data);
      axios
        .get("/routePlan", {
          params: {
            origin: data.origin,
            target: data.target,
          },
        })
        .then((res) => {
          console.log(res);
          pointer.resultFromServer = res.data;
          pointer.PathOptions.disable = false;
          pointer.PathOptions.value = "walk";
          let draw = {
            name: pointer.PathOptions.value,
            size: 2,
            color: "green",
            interval: 0.6,
            trailLength: 1.5,
            duration: 1.5,
            data: [{ path: pointer.resultFromServer.walk.path }],
          };
          let points = {
            name: "points",
            size: 20,
            data: [
              {
                id: "001",
                name: "start",
                lon: this.resultFromServer.origin[0],
                lat: this.resultFromServer.origin[1],
              },
              {
                id: "002",
                name: "stop",
                lon: this.resultFromServer.target[0],
                lat: this.resultFromServer.target[1],
              },
            ],
          };
          this.$emit("draw", {
            line: draw,
            point: points,
          });
        });
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.init();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.selectPathList = this.loadAll();
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style scoped>
/* 卡片框 */
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 16px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 450px;
}
</style>


