<!-- 手机端路径规划 -->
<template>
  <div class="container">
    <el-drawer class="menuDrawer" title="路径规划" :visible.sync="mydrawer" :with-header="false" :modal="false" direction="btt" :size="isSearching?'23%':'30%'">
      <el-form ref="form" :role="roles" :model="navigatorForm" v-show="!isSearching">
        <div class="routerInput">
          <el-form-item prop="origin">
            <el-autocomplete v-model="navigatorForm.origin" :placeholder="originText" clearable :fetch-suggestions="querySearch">
              <div class="title" slot="prepend">起点</div>
            </el-autocomplete>
          </el-form-item>
          <el-form-item prop="target">
            <el-autocomplete v-model="navigatorForm.target" :placeholder="targetText" clearable :fetch-suggestions="querySearch">
              <div class="title" slot="prepend">终点</div>
            </el-autocomplete>
          </el-form-item>
        </div>
        <div class="routeModel">
          <el-form-item class="model">
            <el-switch v-model="navigatorForm.isWalk" active-color="#13ce66" inactive-color="#ff4949" active-text="步行" inactive-text="车辆">
            </el-switch>
            <el-button class="startNav" type="primary" @click="searchRoute()">立即导航</el-button>
          </el-form-item>
        </div>
      </el-form>
      <div class="getRoute" v-show="isSearching">
        <el-page-header @back="goBack" content="路径规划"></el-page-header>
        <el-descriptions class="margin-top" :column="2">
          <el-descriptions-item label="起点">{{navigatorForm.origin}}</el-descriptions-item>
          <el-descriptions-item label="终点">{{navigatorForm.target}}</el-descriptions-item>
        </el-descriptions>
        <div class="carBtn">
          <el-switch class="swt" v-show="!navigatorForm.isWalk" v-model="navigatorForm.isParkNeed" active-color="#13ce66" inactive-color="#ff4949" active-text="停车" inactive-text="不停">
          </el-switch>
          <el-switch class="swt" v-show="navigatorForm.isParkNeed &&!navigatorForm.isWalk" v-model="navigatorForm.isSignUp" active-color="#13ce66" inactive-color="#ff4949" active-text="室内" inactive-text="室外">
          </el-switch>
        </div>
        <div class="buttonDiv">
          <span>{{"共计里程："+length+"公里"}}</span>
          <div class="btn">
            <el-button class="followBtn" type="primary" size="mini" @click="chooseRoute">选择路径</el-button>
          </div>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

import axios from "axios";

axios.defaults.baseURL = "http://172.27.145.91:9011";

let pointer = null;

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  props: ["drawer"],
  data() {
    //这里存放数据
    return {
      mydrawer: this.drawer,
      isSearching: false,

      originText: "请指定出发点",
      targetText: "请指定目的地",

      navigatorForm: {
        origin: "",
        target: "",
        isWalk: true,
        isSignUp: true,
        isParkNeed: false,
      },
      passList: [],
      length: 0,
      selectList: [],

      PathOptions: {
        value: "",
      },

      resultFromServer: null,
      isfirst: true,
      roles: {
        origin: [{ required: true, message: "请输入起始点", trigger: "blur" }],
        target: [{ required: true, message: "请输入目的地", trigger: "blur" }],
      },
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //watch很重要。data（）函数只是在初始化的时候会运行一次。所以总是空。而我们异步过来的数据，需要watch他 才能得到。
  watch: {
    drawer: function (newVal, oldVal) {
      this.mydrawer = newVal;
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    pointer = this;
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.selectList = this.loadAll();
  },
  //生命周期 - 创建之前
  beforeCreate() {},
  //生命周期 - 挂载之前
  beforeMount() {},
  //生命周期 - 更新之前
  beforeUpdate() {},
  //生命周期 - 更新之后
  updated() {},
  //生命周期 - 销毁之前
  beforeDestroy() {},
  //生命周期 - 销毁完成
  destroyed() {},
  //如果页面有keep-alive缓存功能，这个函数会触发
  activated() {},
  //方法集合
  methods: {
    searchRoute() {
      if (
        this.navigatorForm.origin === "" ||
        this.navigatorForm.target === ""
      ) {
        return;
      }
      this.isSearching = true;
    },
    goBack() {
      this.isSearching = false;
      this.isfirst = true;
    },
    loadAll() {
      return this.$store.state.placeList;
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    querySearch(queryString, cb) {
      var restaurants = this.selectList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },

    chooseRoute() {
      if (this.navigatorForm.isWalk) {
        this.PathOptions.value = "walk";
      } else {
        if (this.navigatorForm.isParkNeed) {
          if (this.navigatorForm.isSignUp) {
            this.PathOptions.value = "inside";
          } else {
            this.PathOptions.value = "outside";
          }
        } else {
          this.PathOptions.value = "noparking";
        }
      }
      if (this.isfirst) {
        this.onSubmit();
        this.isfirst = false;
      } else {
        this.pathChange(this.PathOptions.value);
      }
    },

    onSubmit() {
      if (this.navigatorForm.origin != "" && this.navigatorForm.target != "") {
        // todo
        this.AxiosRequest({
          origin: this.navigatorForm.origin,
          target: this.navigatorForm.target,
        });
      } else {
        this.$notify({
          title: "警告",
          message: "必须填写起始点与目标点",
          type: "warning",
        });
      }
    },

    AxiosRequest(data) {
      axios
        .get("/routePlan", {
          params: {
            origin: data.origin,
            target: data.target,
          },
        })
        .then((res) => {
          this.resultFromServer = res.data;
          this.length = this.resultFromServer.inside.walkdistance / 1000;
          let draw = {
            name: this.PathOptions.value,
            size: 2,
            color: "green",
            interval: 0.6,
            trailLength: 1.5,
            duration: 1.5,
            data: [{ path: this.resultFromServer.walk.path }],
          };
          let points = {
            name: "points",
            size: 10,
            data: [
              {
                id: "001",
                name: "start",
                lon: this.resultFromServer.origin[0],
                lat: this.resultFromServer.origin[1],
              },
              {
                id: "002",
                name: "stop",
                lon: this.resultFromServer.target[0],
                lat: this.resultFromServer.target[1],
              },
            ],
          };
          let polygons = {
            name: this.PathOptions.value,
            color: "#B1DDF0",
            size: 2,
            data: [{ path: this.resultFromServer.inside.polygon }],
          };
          this.$emit("draw", {
            line: draw,
            point: points,
            polygon: polygons,
          });
        });
    },

    pathChange(val) {
      switch (val) {
        case "walk":
          this.$emit("draw", {
            line: {
              name: val,
              size: 2,
              color: "green",
              interval: 0.6,
              trailLength: 1.5,
              duration: 1.5,
              data: [{ path: pointer.resultFromServer.walk.path }],
            },
            point: {
              name: "points",
              size: 10,
              data: [
                {
                  id: "001",
                  name: "start",
                  lon: this.resultFromServer.origin[0],
                  lat: this.resultFromServer.origin[1],
                },
                {
                  id: "002",
                  name: "stop",
                  lon: this.resultFromServer.target[0],
                  lat: this.resultFromServer.target[1],
                },
              ],
            },
          });
          break;
        case "noparking":
          this.$emit("draw", {
            line: {
              name: val,
              size: 2,
              color: "blue",
              interval: 0.6,
              trailLength: 1.5,
              duration: 1.5,
              data: [{ path: pointer.resultFromServer.noparking.path }],
            },
            point: {
              name: "points",
              size: 10,
              data: [
                {
                  id: "001",
                  name: "start",
                  lon: this.resultFromServer.origin[0],
                  lat: this.resultFromServer.origin[1],
                },
                {
                  id: "002",
                  name: "stop",
                  lon: this.resultFromServer.target[0],
                  lat: this.resultFromServer.target[1],
                },
              ],
            },
          });
          break;
        case "inside":
          this.$emit("draw", {
            line: {
              name: "drive",
              one: {
                name: val + "car",
                size: 2,
                color: "blue",
                interval: 0.6,
                trailLength: 1.5,
                duration: 1.5,
                data: [{ path: pointer.resultFromServer.inside.carpath }],
              },
              two: {
                name: val + "walk",
                size: 2,
                color: "green",
                interval: 0.6,
                trailLength: 1.5,
                duration: 1.5,
                data: [{ path: pointer.resultFromServer.inside.walkpath }],
              },
            },
            point: {
              name: "points",
              size: 10,
              data: [
                {
                  id: "001",
                  name: "start",
                  lon: this.resultFromServer.origin[0],
                  lat: this.resultFromServer.origin[1],
                },
                {
                  id: "002",
                  name: "stop",
                  lon: this.resultFromServer.target[0],
                  lat: this.resultFromServer.target[1],
                },
              ],
            },
            polygon: {
              name: pointer.PathOptions.value,
              color: "#B1DDF0",
              size: 2,
              data: [{ path: pointer.resultFromServer.inside.polygon }],
            },
          });
          break;
        case "outside":
          this.$emit("draw", {
            line: {
              name: "drive",
              one: {
                name: val + "car",
                size: 2,
                color: "blue",
                interval: 0.6,
                trailLength: 1.5,
                duration: 1.5,
                data: [{ path: pointer.resultFromServer.outside.carpath }],
              },
              two: {
                name: val + "walk",
                size: 2,
                color: "green",
                interval: 0.6,
                trailLength: 1.5,
                duration: 1.5,
                data: [{ path: pointer.resultFromServer.outside.walkpath }],
              },
            },
            point: {
              name: "points",
              size: 10,
              data: [
                {
                  id: "001",
                  name: "start",
                  lon: this.resultFromServer.origin[0],
                  lat: this.resultFromServer.origin[1],
                },
                {
                  id: "002",
                  name: "stop",
                  lon: this.resultFromServer.target[0],
                  lat: this.resultFromServer.target[1],
                },
              ],
            },
          });
          break;
        default:
          break;
      }
    },
  },
};
</script>
<style scoped>
.container {
  width: 100%;
  height: 100%;
}
.menuDrawer {
  position: absolute;
  left: 0%;
  bottom: 50px;
}

.routerInput {
  padding: 5px 20px;
  padding-top: 15px;
}

.startNav {
  position: absolute;
  right: 10px;
}

.swt {
  margin-left: 10px;
  margin-bottom: 10px;
}

.routeModel .model {
  font-size: 14px;
  margin: 0 20px;
}

.el-drawer__body {
  border-radius: 10px;
}
.getRoute {
  margin: 10px 10px;
}

.routeContent {
  position: relative;
  margin: 10px 20px;
}
.margin-top {
  margin: 10px 10px;
}
.buttonDiv {
  margin-left: 10px;
}
.btn {
  float: right;
}
</style>


