import Vue from 'vue'
import Router from 'vue-router'
import PCpage from '../views/PCpage.vue'
import mobile from '../views/Mobilepage.vue'
import home from '../views/home.vue'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/pc',
            name: 'PCpage',
            component: PCpage
        },
        {
            path: '/',
            name: 'mobile',
            component: mobile
        },
        {
            path: '/',
            name: 'home',
            component: home
        }

    ]
})


