<!--  -->
<template>
    <div id="sys-setting" style="position:absolute;display: none;
    left: 73%;top: 1%; z-index: 10;">
        <el-card class="box-card" shadow="always">
            <div slot="header" class="clearfix">
                <span>默认项设置</span>
                <el-button style="float: right; padding: 3px 0" type="text" @click="onClose">关闭</el-button>
            </div>
            <div class="text item">
                <el-tabs tab-position="left" style="height: 200px;">
                    <el-tab-pane label="目的地导航">
                        <el-form label-width="100px">
                            <el-tooltip class="item" effect="light" content="导航时根据定位自动填入出发地" placement="bottom">
                                <el-form-item label="自动填入定位">
                                    <el-switch
                                        v-model="isAutoLocated"
                                        active-color="#13ce66"
                                        inactive-color="#ff4949">
                                    </el-switch>
                                </el-form-item>
                            </el-tooltip>
                        </el-form>
                    </el-tab-pane>
                    <el-tab-pane label="路径规划">规划</el-tab-pane>
                    <el-tab-pane label="系统设置">系统</el-tab-pane>
                </el-tabs>
            </div>
        </el-card>
    </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
//import引入的组件需要注入到对象中才能使用
components: {},
data() {
//这里存放数据
    return {
        isAutoLocated: this.$store.state.auto_orient
    };
},
//监听属性 类似于data概念
computed: {},
//监控data中的数据变化
watch: {},
//方法集合
methods: {
    onShow(){
        document.getElementById('sys-setting').style.display = 'block';
    },

    onClose(){
        document.getElementById('sys-setting').style.display = 'none';
    }
},
//生命周期 - 创建完成（可以访问当前this实例）
created() {

},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {}, //生命周期 - 创建之前
beforeMount() {}, //生命周期 - 挂载之前
beforeUpdate() {}, //生命周期 - 更新之前
updated() {}, //生命周期 - 更新之后
beforeDestroy() {}, //生命周期 - 销毁之前
destroyed() {}, //生命周期 - 销毁完成
activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped>
    .text {
        font-size: 14px;
    }

    .item {
        margin-bottom: 18px;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    .clearfix:after {
        clear: both
    }

    .box-card {
        width: 480px;
    }
</style>


