<!--  -->
<template>
    <div id="running-helper" class='running-helper' style="position:absolute;display: none;
    left: 77%;top: 1%; z-index: 100;">
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>{{title}}</span>
                <el-button style="float: right; padding: 3px 0" type="text" @click="onClose">关闭</el-button>
            </div>
            <div class="text item">
                <el-form ref="running_form" :model="RunningForm" label-width="80px">
                    <el-form-item label="起点" prop="origin">
                        <el-autocomplete
                        class="inline-input"
                        v-model="RunningForm.origin"
                        :fetch-suggestions="querySearch"
                        placeholder="请输入起点"
                        @select="handleSelect"
                        ></el-autocomplete>
                    </el-form-item>
                    <el-form-item label="终点" prop="target">
                        <el-autocomplete
                        class="inline-input"
                        v-model="RunningForm.target"
                        :fetch-suggestions="querySearch"
                        placeholder="请输入起点"
                        @select="handleSelect"
                        ></el-autocomplete>
                    </el-form-item>
                    <el-form-item label="最小里程" prop="num">
                        <el-input-number v-model="RunningForm.num" :step="0.5"></el-input-number>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">路径规划</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-card>
    </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import axios from 'axios'

let pointer = null;

export default {
//import引入的组件需要注入到对象中才能使用
components: {},
data() {
//这里存放数据
return {
    selectList:[],

    title:'乐跑助手',
    isSubmit:false,
    RunningForm:{
        origin:'',
        target:'',
        num:1.5
    },

    resultFromServer:null
};
},
//监听属性 类似于data概念
computed: {},
//监控data中的数据变化
watch: {},
//方法集合
methods: {
    onShow(){
        document.getElementById('running-helper').style.display = 'block';
    },

    onClose(){
        this.refresh();
        document.getElementById('running-helper').style.display = 'none';
        this.$emit('close');
    },

    onSubmit(){
        if(this.RunningForm.origin != '' && this.RunningForm.target != ''){
            // todo
            this.AxiosRequest({
                origin:this.RunningForm.origin,
                target:this.RunningForm.target,
                length:this.RunningForm.num
            });
        }else{
            this.$notify({
                title: '警告',
                message: '必须填写起始点与目标点',
                type: 'warning'
            });
        }
    },

    refresh(){
        this.title = "乐跑助手";
        this.RunningForm.origin = '',
        this.RunningForm.target = '',
        this.RunningForm.num = 1.5
    },

    AxiosRequest(data){
        axios.defaults.baseURL = 'http://172.27.145.91:9011';
        axios.get('/runAssistant',{
            params:{
                origin:data.origin,
                target:data.target,
                length:data.length
            }
        }).then(res=>{
            console.log(res);
            pointer.resultFromServer = res.data;
            // 添加里程
            pointer.title = `乐跑助手: 总里程 ${pointer.resultFromServer.distance / 1000} km`;
            let draw = {
                name:'running',
                size:2,
                color:"red",
                interval:1.5,
                trailLength:1,
                duration:5,
                data:[{path:pointer.resultFromServer.path}]
            };
            let points = {
                name:'points',
                size:20,
                data:[
                    {
                        id:"001",
                        name:"start",
                        lon:this.resultFromServer.origin[0],
                        lat:this.resultFromServer.origin[1]
                    },
                    {
                        id:"002",
                        name:"stop",
                        lon:this.resultFromServer.target[0],
                        lat:this.resultFromServer.target[1]
                    },
                    {
                        id:"003",
                        name:"pounch",
                        lon:this.resultFromServer.pounch1.coordinate[0],
                        lat:this.resultFromServer.pounch1.coordinate[1]
                    },
                    {
                        id:"004",
                        name:"pounch",
                        lon:this.resultFromServer.pounch2.coordinate[0],
                        lat:this.resultFromServer.pounch2.coordinate[1]
                    },
                ]
            };
            this.$emit('drawrun',{
                line: draw,
                point: points
            });
        })
    },



    // 设置联想输入
    querySearch(queryString, cb) {
        var list = this.selectList;
        var results = queryString ? list.filter(this.createFilter(queryString)) : list;
        // 调用 callback 返回建议列表的数据
        cb(results);
    },
    createFilter(queryString) {
        return (list) => {
          return (list.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
    },
    loadAll(){
        return this.$store.state.placeList
    },
    handleSelect(item){
        console.log(item)
    }
},
//生命周期 - 创建完成（可以访问当前this实例）
created() {
    pointer = this;
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {
    this.selectList = this.loadAll();
},
beforeCreate() {}, //生命周期 - 创建之前
beforeMount() {}, //生命周期 - 挂载之前
beforeUpdate() {}, //生命周期 - 更新之前
updated() {}, //生命周期 - 更新之后
beforeDestroy() {}, //生命周期 - 销毁之前
destroyed() {}, //生命周期 - 销毁完成
activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped>
    .text {
        font-size: 14px;
    }

    .item {
        margin-bottom: 18px;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    .clearfix:after {
        clear: both
    }

    .box-card {
        width: 400px;
    }


</style>


