<!--  -->
<template>
  <div id='date-log__container' style="position:absolute;display: none;
        left: 73%;top: 1%; z-index: 100;">
    <div class="date-log__card">
      <el-card class="box-card" shadow="always">
        <div slot="header" class="clearfix">
          <span>{{title}}</span>
          <el-button style="float: right; padding: 3px 0" type="text" @click="onClose">关闭</el-button>
        </div>
        <div class="text item">
          <template v-if="!isCalculate">
            <el-form ref="Dateform" :model="DateLog" label-width="0px" :disabled="DateLog.isDisabled">
              <el-form-item>
                <el-input v-model="DateLog.name" :placeholder="DateLog.nameText" style="width:54%">
                  <el-select v-model="DateLog.select" slot="prepend" placeholder="优先度" style="width:90px;">
                    <el-option label="默认" value="1"></el-option>
                    <el-option label="优先" value="2"></el-option>
                  </el-select>
                </el-input>
                <el-autocomplete class="inline-input" v-model="DateLog.place" :fetch-suggestions="querySearch" :placeholder="DateLog.placeText" @select="handleSelect"></el-autocomplete>
                <!-- <el-input v-model="DateLog.place" :placeholder="DateLog.placeText" style="width:45%"></el-input> -->
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="addToList">{{DateLog.addText}}</el-button>
                <el-popover placement="top" width="160" v-model="isImport">
                  <el-form label-width="0px">
                    <el-form-item>
                      <el-input v-model="Corse.account" placeholder="学号"></el-input>
                    </el-form-item>
                    <el-form-item>
                      <el-input v-model="Corse.password" placeholder="密码" :show-password="Corse.isShowPassword"></el-input>
                    </el-form-item>
                    <el-form-item>
                      <el-button type="primary" @click="importNow">导入</el-button>
                      <el-button type="primary" @click="importCancel">取消</el-button>
                    </el-form-item>
                  </el-form>
                  <el-button type="primary" slot="reference" style="margin-left:0;">{{DateLog.importText}}</el-button>
                </el-popover>
              </el-form-item>
              <el-form-item>
                <el-table :data="DateLog.targetList" style="width: 100%" height="300">
                  <el-table-column label="备注" width="165">
                    <template slot-scope="scope">
                      <span style="margin-left: 10px">{{ scope.row.name }}</span>
                    </template>
                  </el-table-column>
                  <el-table-column label="地点" width="140">
                    <template slot-scope="scope">
                      <span style="margin-left: 10px">{{ scope.row.place }}</span>
                    </template>
                  </el-table-column>
                  <el-table-column label="操作">
                    <template slot-scope="scope">
                      <el-button size="mini" type="warning" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
                      <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSubmit">确定</el-button>
              </el-form-item>
            </el-form>
          </template>
          <template v-else>
            <el-table height="500" :data="ResultTable" style="width: 100%">
              <el-table-column prop="name" label="备注" width="180">
              </el-table-column>
              <el-table-column prop="place" label="地点" width="180">
              </el-table-column>
            </el-table>
          </template>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import axios from "axios";
axios.defaults.baseURL = "http://172.27.145.91:9011";

let pointer = null;

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      selectList: [],

      isCalculate: false,

      title: "日程规划——设计表",

      // 该变量用于标记当前需要修改的数据下标
      currentIndex: -1,
      // 日程计划表中的表单
      DateLog: {
        isDisabled: false,
        name: "",
        place: "",
        select: "1",
        nameText: "输入备注",
        placeText: "输入地点",

        addText: "添加",
        importText: "导入课程表",

        targetList: [],

        customList: [],
      },

      isImport: false,

      // 用于输入学号密码对接教务系统
      Corse: {
        account: "",
        password: "",
        isShowPassword: false,
      },

      // 提交整个日程计划后用于保存从服务端得到的日程排班数据
      ResultTable: [],

      // 保存从本地得到的日程json数据
      CorseJsonData: null,
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    // 初始化必要参数
    init() {
      pointer = this;
    },

    refresh() {
      this.$refs["Dateform"].resetFields();
    },

    // 提交整个日程计划，进行路径安排
    onSubmit() {
      let arr = [];
      for (let i in this.DateLog.targetList) {
        if (this.DateLog.targetList[i].priority != "0") {
          arr.push({
            name: this.DateLog.targetList[i].name,
            place: this.DateLog.targetList[i].place,
            priority: this.DateLog.targetList[i].priority + "",
          });
        }
      }
      let ness = [
        "学一食堂北门", //早饭
        "公教一",
        "公教一", //上午两节课
        "教职工食堂东门", //午饭
        "",
        "公教一", //下午两节课
        "教职工食堂东门", //晚饭
        "",
        "", //晚上两节课
      ];
      console.log(arr);
      axios
        .get("/schedulePlan", {
          params: {
            scheduleJson: {
              custom: arr,
              necessary: ness,
            },
          },
        })
        .then((data) => {
          console.log(data.data);
          for (let i in data.data.events) {
            this.ResultTable.push(data.data.events[i]);
          }

          // 画线
          let draw = {
            name: "规划",
            size: 2,
            color: "red",
            interval: 0.6,
            trailLength: 1.5,
            duration: 1.5,
            data: [{ path: data.data.path }],
          };
          this.$emit("drawdate", draw);
          this.isCalculate = true;
        });
    },

    // 该方法用于假设与教务处对接并自动填充日程计划表
    importNow() {
      // 关闭
      this.isImport = false;

      // 读取本地课程json文件并自动填充到表格中
      this.$message("正在对接教务处网站...");
      this.DateLog.isDisabled = true;
      this.DateLog.importText = "努力导入中";
      fetch("/static/Plugins/Json/corse.json")
        .then((data) => data.json())
        .then((data) => {
          let res = [];
          for (let i = 0; i < data.data.length; i++) {
            let temp = data.data[i].list;
            if (temp.length > 0) {
              for (let j in temp) {
                res.push({
                  name: temp[j].name,
                  place: temp[j].place,
                  priority: temp[j].priority,
                });
              }
            }
          }

          setTimeout(() => {
            pointer.DateLog.targetList = res;

            pointer.DateLog.isDisabled = false;
            pointer.DateLog.importText = "导入课程表";

            pointer.$message({
              message: "课程表导入成功!",
              type: "success",
            });
          }, 3000);
        });
    },

    importCancel() {
      // 关闭
      this.isImport = false;
    },

    // 日程表格中的编辑按钮
    handleEdit(index, row) {
      this.currentIndex = index;
      this.DateLog.addText = "修改";
      this.DateLog.name = row.name;
      this.DateLog.place = row.place;
      this.DateLog.select = row.priority == 2 ? "1" : "2";

      console.log(index, row);
    },

    // 日程表格中的删除按钮
    handleDelete(index) {
      this.DateLog.targetList.splice(index, 1);
    },

    // 添加到表格按钮
    addToList() {
      if (this.currentIndex >= 0) {
        this.DateLog.targetList[this.currentIndex].name = this.DateLog.name;
        this.DateLog.targetList[this.currentIndex].place = this.DateLog.place;
        this.DateLog.targetList[this.currentIndex].priority =
          this.DateLog.select == "1" ? 2 : 1;
        this.DateLog.name = "";
        this.DateLog.place = "";
        this.DateLog.select = "";
        this.currentIndex = -1;
        this.DateLog.addText = "添加";
        return;
      }
      if (this.DateLog.name != "" && this.DateLog.place != "") {
        this.DateLog.targetList.push({
          name: this.DateLog.name,
          place: this.DateLog.place,
          priority: this.DateLog.select == "默认" ? 2 : 1,
        });
        this.DateLog.name = "";
        this.DateLog.place = "";
        this.DateLog.select = "";
      }
    },

    // 显示此界面
    onShow() {
      this.refresh();
      document.getElementById("date-log__container").style.display = "block";
    },

    // 关闭此界面
    onClose() {
      this.isCalculate = false;
      document.getElementById("date-log__container").style.display = "none";
      this.ResultTable = [];
      this.$emit("close");
    },

    querySearch(queryString, cb) {
      var list = this.selectList;
      var results = queryString
        ? list.filter(this.createFilter(queryString))
        : list;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return (list) => {
        return (
          list.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    loadAll() {
      return this.$store.state.placeList;
    },
    handleSelect(item) {
      console.log(item);
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.init();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.selectList = this.loadAll();
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style scoped>
/* 卡片框 */
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 16px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 500px;
}
</style>

<style>
::-webkit-scrollbar {
  width: 5px;
  background-color: #fff;
}

::-webkit-scrollbar-track {
  background-color: #fff;
}

::-webkit-scrollbar-thumb {
  background: lightgray;
}

.el-table .cell {
  padding-left: 0px;
  padding-right: 0px;
}
</style>


