<!--  -->
<template>
<div id="sys-about" style="position: absolute; display:none">
    <el-dialog
        center
        title="未来之路——一站式校园助手"
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose">
        <span>“未来之路”一站式校园助手系统v1.0,由以下成员共同完成:</span><br>
        <span>114192 王立增</span><br>
        <span>114193 关国梁</span><br>
        <span>114193 李泽诚</span><br>
        <span>114193 杨婕</span><br>
        <span>114192 张晗</span><br>
        <span slot="footer" class="dialog-footer">
            此系统仅用于3S综合实习课程
        </span>
    </el-dialog>
</div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
//import引入的组件需要注入到对象中才能使用
components: {},
data() {
    //这里存放数据
    return {
        dialogVisible:false,
    };
},
//监听属性 类似于data概念
computed: {},
//监控data中的数据变化
watch: {},
//方法集合
methods: {
    onShow(){
        document.getElementById('sys-about').style.display = 'block';
        this.dialogVisible = true;
    },

    handleClose(done){
        document.getElementById('sys-about').style.display = 'none';
        done();
    }
},
//生命周期 - 创建完成（可以访问当前this实例）
created() {

},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {}, //生命周期 - 创建之前
beforeMount() {}, //生命周期 - 挂载之前
beforeUpdate() {}, //生命周期 - 更新之前
updated() {}, //生命周期 - 更新之后
beforeDestroy() {}, //生命周期 - 销毁之前
destroyed() {}, //生命周期 - 销毁完成
activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped>

</style>


