<!--手机端日程规划  -->
<template>
  <div class='container'>
    <el-drawer :modal-append-to-body="true" class="menuDrawer" title="路径规划" :visible.sync="mydrawer" :with-header="false" :modal="false" direction="btt" :size="isSearching?'15%':'70%'">
      <el-form ref="Dateform" :model="DateLog" v-show="!isSearching">
        <el-form-item>
          <el-autocomplete placeholder="输入地点" v-model="DateLog.target" class="input-with-select" ref="targetInput" :fetch-suggestions="querySearch">
            <el-select class="targetSelect" v-model="DateLog.primary" slot="prepend" placeholder="优先级">
              <el-option label="默认" :value="1"></el-option>
              <el-option label="优先" :value="2"></el-option>
            </el-select>
          </el-autocomplete>
          <el-input placeholder="请输入内容" v-model="DateLog.remark" clearable class="remarkInput" ref="remarkInput">
            <div class="remark" slot="prepend">备 注</div>
          </el-input>
          <el-button class="Inputbutton" v-show="!isEdit" type="primary" @click="addToList()">添加</el-button>
          <el-button class="Inputbutton" v-show="isEdit" type="success" @click="upDateToList()">保存</el-button>
          <el-button class="Inputbutton" type="primary" :disabled="isEdit" @click="importNow()">导入课程表</el-button>
          <el-button class="Inputbutton" type="primary" :disabled="isEdit" @click="getDateRoute()">查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-table :data="DateLog.targetList" style="width: 100%" height="260" :header-cell-style="{'text-align':'center'}" :row-class-name="tableRowClassName">

            <el-table-column label="途径地点" width="100%" align="center">
              <div slot-scope="scope">
                <!-- <i class="el-icon-time"></i> -->
                <span style="margin-left: 10px">{{ scope.row.place }}</span>
              </div>
            </el-table-column>

            <el-table-column label="备注" width="110%" align="center">
              <div slot-scope="scope">
                <span style="margin-left: 10px">{{ scope.row.name }}</span>
              </div>
            </el-table-column>

            <el-table-column label="操作" align="center">
              <div slot-scope="scope">
                <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)" :disabled="isEdit">删除</el-button>
              </div>
            </el-table-column>
          </el-table>
        </el-form-item>
      </el-form>
      <div class="getRoute" v-show="isSearching">
        <el-page-header @back="goBack" content="日程规划"></el-page-header>
        <div class="routeContent">
          <span class="totoLength">{{"共计里程"+length+"公里"}}</span>
          <!-- <el-button class="followBtn" size="mini" type="primary">路径漫游</el-button> -->
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

import axios from "axios";
axios.defaults.baseURL = "http://172.27.145.91:9011";

let pointer = null;

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  props: ["drawer"],
  data() {
    //这里存放数据
    return {
      mydrawer: this.drawer,
      isEdit: false,
      isSearching: false,
      currentIndex: 0,
      DateLog: {
        primary: 1,
        target: "",
        remark: "",
        targetList: [],
      },
      selectList: [],
      length: 0,
      numEvent: 0,

      // 提交整个日程计划后用于保存从服务端得到的日程排班数据
      ResultTable: [],

      // 保存从本地得到的日程json数据
      CorseJsonData: null,
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {
    drawer: function (newVal, oldVal) {
      this.mydrawer = newVal;
    },
    mydrawer: function (newVal, oldVal) {
      if (newVal === false) {
        this.$bus.$emit("cancelpick");
      }
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    pointer = this;
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.selectList = this.loadAll();
  },
  //生命周期 - 创建之前
  beforeCreate() {},
  //生命周期 - 挂载之前
  beforeMount() {},
  //生命周期 - 更新之前
  beforeUpdate() {},
  //生命周期 - 更新之后
  updated() {},
  //生命周期 - 销毁之前
  beforeDestroy() {},
  //生命周期 - 销毁完成
  destroyed() {},
  //如果页面有keep-alive缓存功能，这个函数会触发
  activated() {},
  //方法集合
  methods: {
    tableRowClassName({ row, rowIndex }) {
      if (row.priority === 2) {
        return "warning-row";
      }
      return "";
    },
    addToList() {
      if (this.DateLog.target === "") {
        return;
      }
      var item = {
        priority: this.DateLog.primary,
        place: this.DateLog.target,
        name: this.DateLog.remark,
      };
      this.DateLog.targetList.push(item);
      this.DateLog.target = "";
      this.DateLog.remark = "";
      this.numEvent++;
    },

    handleEdit(index, row) {
      this.isEdit = true;
      this.currentIndex = index;
      this.DateLog.primary = row.priority;
      this.DateLog.target = row.place;
      this.DateLog.remark = row.name;
    },
    upDateToList() {
      this.DateLog.targetList[this.currentIndex].name = this.DateLog.remark;
      this.DateLog.targetList[this.currentIndex].place = this.DateLog.target;
      this.DateLog.targetList[this.currentIndex].priority =
        this.DateLog.primary;
      this.isEdit = false;
      this.DateLog.target = "";
      this.DateLog.remark = "";
    },
    handleDelete(index, row) {
      this.DateLog.targetList = this.DateLog.targetList.filter((todo) => {
        return todo != row;
      });
      this.numEvent--;
    },

    getDateRoute() {
      if (this.DateLog.targetList.length < 2) {
        return;
      }
      this.isSearching = true;
      this.onSubmit();
    },
    goBack() {
      this.isSearching = false;
    },
    loadAll() {
      return this.$store.state.placeList;
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    querySearch(queryString, cb) {
      var restaurants = this.selectList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },

    // 该方法用于假设与教务处对接并自动填充日程计划表
    importNow() {
      // 读取本地课程json文件并自动填充到表格中
      this.$message("正在对接教务处网站...");
      fetch("/static/Plugins/Json/corse.json")
        .then((data) => data.json())
        .then((data) => {
          let res = [];
          for (let i = 0; i < data.data.length; i++) {
            let temp = data.data[i].list;
            if (temp.length > 0) {
              for (let j in temp) {
                res.push({
                  name: temp[j].name,
                  place: temp[j].place,
                  priority: temp[j].priority,
                });
              }
            }
          }

          setTimeout(() => {
            this.DateLog.targetList = res;

            this.$message({
              message: "课程表导入成功!",
              type: "success",
            });
          }, 3000);
        });
    },

    // 提交整个日程计划，进行路径安排
    onSubmit() {
      let arr = [];
      for (let i in this.DateLog.targetList) {
        if (this.DateLog.targetList[i].priority != "0") {
          arr.push({
            name: this.DateLog.targetList[i].name,
            place: this.DateLog.targetList[i].place,
            priority: this.DateLog.targetList[i].priority + "",
          });
        }
      }
      let ness = [
        "学一食堂北门", //早饭
        "公教一",
        "公教一", //上午两节课
        "教职工食堂东门", //午饭
        "",
        "公教一", //下午两节课
        "教职工食堂东门", //晚饭
        "",
        "", //晚上两节课
      ];
      axios
        .get("/schedulePlan", {
          params: {
            scheduleJson: {
              custom: arr,
              necessary: ness,
            },
          },
        })
        .then((data) => {
          this.length = data.data.distance / 1000;
          for (let i in data.data.events) {
            this.ResultTable.push(data.data.events[i]);
          }

          // 画线
          let draw = {
            name: "规划",
            size: 2,
            color: "red",
            interval: 0.6,
            trailLength: 1.5,
            duration: 1.5,
            data: [{ path: data.data.path }],
          };
          this.$emit("drawdate", draw);
        });
    },
  },
};
</script>
<style scoped>
.container {
  width: 100%;
  height: 100%;
}
.menuDrawer {
  position: absolute;
  left: 0%;
  bottom: 50px;
  overflow: hidden;
}

.input-with-select {
  margin: 20px 10px;
  width: 90%;
}

.remarkInput {
  width: 90%;
  margin: 0 10px;
}
.remark {
  width: 50px;
}

.input-with-select .targetSelect {
  width: 90px;
  font-size: 14px;
}

.Inputbutton {
  position: relative;
  right: -20%;
  margin-top: 20px;
}

.getRoute {
  margin: 10px 10px;
}
.routeContent {
  position: relative;
  margin: 10px 20px;
}
.routeContent .followBtn {
  position: absolute;
  right: 10px;
}
</style>
<style >
.el-table .warning-row {
  background: oldlace;
}

.el-dialog__wrapper {
  width: 100%;
  height: 20%;
}
.el-dialog {
  margin: 0;
  width: auto;
}
</style>


