<!-- 手机端步道乐跑界面 -->
<template>
  <div class='container'>
    <el-drawer class="menuDrawer" title="乐跑助手" :visible.sync="mydrawer" :with-header="false" :modal="false" direction="btt" :size="isSearching?'20%':'50%'">
      <div v-show="!isSearching">
        <el-form ref="runningForm" :model="RunRoute">
          <el-form-item class="routeInput">
            <el-autocomplete class="input" placeholder="请输入起点" v-model="RunRoute.start" :fetch-suggestions="querySearch">
              <div slot="prepend">起点</div>
            </el-autocomplete>
            <el-autocomplete class="input" placeholder="请输入终点" v-model="RunRoute.target" :fetch-suggestions="querySearch">
              <div slot="prepend">终点</div>
            </el-autocomplete>
          </el-form-item>
          <el-form-item>
            <el-input-number class="numInput" size="medium" v-model="RunRoute.length" :precision="1" :step="0.1" :min="1.5" :max="3"></el-input-number>
            <el-button class="button" type="primary" size="medium" @click="submitPoint()">确定</el-button>
          </el-form-item>
        </el-form>

        <el-image class="weatherImage" :src="weather.weatherUrl" fit="contain">
        </el-image>
        <el-descriptions title="天气情况" :column="2" class="weaInfo">
          <el-descriptions-item label="天气">{{weather.wea}}</el-descriptions-item>
          <el-descriptions-item label="空气质量">{{weather.air}}</el-descriptions-item>
          <el-descriptions-item label="气温">{{weather.temperature+"℃"}}</el-descriptions-item>
          <el-descriptions-item label="湿度">{{weather.humidity}}</el-descriptions-item>
        </el-descriptions>
      </div>
      <div class="getRoute" v-show="isSearching">
        <el-page-header @back="goBack" content="途经点"></el-page-header>
        <el-descriptions class="margin-top" :column="2">
          <el-descriptions-item label="途经点1">{{passList[0]}}</el-descriptions-item>
          <el-descriptions-item label="途经点2">{{passList[1]}}</el-descriptions-item>
        </el-descriptions>
        <div class="buttonDiv">
          <span>{{"共计里程："+length+"公里"}}</span>
          <div class="btn">
            <el-button class="followBtn" type="primary" size="mini" @click="onSubmit">切换路线</el-button>
            <!-- <el-button class="followBtn" type="primary" size="mini">开始乐跑</el-button> -->
          </div>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import axios from "axios";

let pointer = null;

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  props: ["drawer"],

  data() {
    //这里存放数据
    return {
      mydrawer: this.drawer,
      isSearching: false,
      RunRoute: {
        start: "",
        target: "",
        length: 1.5,
      },
      passList: ["", ""],
      length: 1.5,
      weather: {
        wea: "晴",
        temperature: 21,
        air: 0,
        humidity: "10%",
        weatherUrl: "../../static/Source/Images/qing.png",
      },
      selectList: [],
      resultFromServer: null,
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //watch很重要。data（）函数只是在初始化的时候会运行一次。所以总是空。而我们异步过来的数据，需要watch他 才能得到。
  watch: {
    drawer: function (newVal, oldVal) {
      this.mydrawer = newVal;
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    pointer = this;
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.getWeather();
    this.selectList = this.loadAll();
  },
  //生命周期 - 创建之前
  beforeCreate() {},
  //生命周期 - 挂载之前
  beforeMount() {},
  //生命周期 - 更新之前
  beforeUpdate() {},
  //生命周期 - 更新之后
  updated() {},
  //生命周期 - 销毁之前
  beforeDestroy() {},
  //生命周期 - 销毁完成
  destroyed() {},
  //如果页面有keep-alive缓存功能，这个函数会触发
  activated() {},
  //方法集合
  methods: {
    submitPoint() {
      if (this.RunRoute.start === "" || this.RunRoute.target === "") {
        return;
      }
      this.isSearching = true;
      this.onSubmit();
    },
    goBack() {
      this.isSearching = false;
    },
    getWeather() {
      var newWeather;
      this.$axios
        .get(
          "https://www.tianqiapi.com/free/day?appid=75698768&appsecret=7LyHELsc&city=武汉"
        )
        .then((reponse) => {
          this.weather.temperature = reponse.data.tem;
          this.weather.air = reponse.data.tem;
          this.weather.humidity = reponse.data.humidity;
          this.weather.wea = reponse.data.wea;
          this.weather.weatherUrl =
            "../../static/Source/Images/" + reponse.data.wea_img + ".png";
        })
        .catch((err) => {
          console.log(err);
        });
    },
    loadAll() {
      return this.$store.state.placeList;
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    querySearch(queryString, cb) {
      var restaurants = this.selectList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },

    onSubmit() {
      if (this.RunRoute.start != "" && this.RunRoute.target != "") {
        // todo
        this.AxiosRequest({
          origin: this.RunRoute.start,
          target: this.RunRoute.target,
          length: this.RunRoute.length,
        });
      } else {
        this.$notify({
          title: "警告",
          message: "必须填写起始点与目标点",
          type: "warning",
        });
      }
    },

    AxiosRequest(data) {
      axios.defaults.baseURL = "http://172.27.145.91:9011";
      axios
        .get("/runAssistant", {
          params: {
            origin: data.origin,
            target: data.target,
            length: data.length,
          },
        })
        .then((res) => {
          pointer.resultFromServer = res.data;
          // 添加里程
          pointer.length = pointer.resultFromServer.distance / 1000;
          this.passList[0] = pointer.resultFromServer.pounch1.name;
          this.passList[1] = pointer.resultFromServer.pounch2.name;
          let draw = {
            name: "running",
            size: 2,
            color: "red",
            interval: 1.5,
            trailLength: 1,
            duration: 5,
            data: [{ path: pointer.resultFromServer.path }],
          };
          let points = {
            name: "points",
            size: 10,
            data: [
              {
                id: "001",
                name: "start",
                lon: this.resultFromServer.origin[0],
                lat: this.resultFromServer.origin[1],
              },
              {
                id: "002",
                name: "stop",
                lon: this.resultFromServer.target[0],
                lat: this.resultFromServer.target[1],
              },
              {
                id: "003",
                name: "pounch",
                lon: this.resultFromServer.pounch1.coordinate[0],
                lat: this.resultFromServer.pounch1.coordinate[1],
              },
              {
                id: "004",
                name: "pounch",
                lon: this.resultFromServer.pounch2.coordinate[0],
                lat: this.resultFromServer.pounch2.coordinate[1],
              },
            ],
          };
          this.$emit("drawrun", {
            line: draw,
            point: points,
          });
        });
    },
  },
};
</script>
<style scoped>
.container {
  width: 100%;
  height: 100%;
}
.menuDrawer {
  position: absolute;
  left: 0%;
  bottom: 50px;
  overflow: hidden;
}

.routeInput .input {
  margin: 10px 10px;
  width: 90%;
}

.numInput {
  margin: 0 10px;
}

.button {
  margin-left: 50px;
}

.weatherImage {
  float: left;
  margin-left: 10px;
  margin-top: 10px;
  width: 40%;
  height: 100px;
}

.weaInfo {
  margin-top: 10px;
  float: right;
  width: 50%;
}

.getRoute {
  margin: 10px 10px;
}

.routeContent {
  position: relative;
  margin: 10px 20px;
}
.margin-top {
  margin: 10px 10px;
}
.buttonDiv {
  margin-left: 10px;
}
.btn {
  display: inline-block;
  margin-left: 50px;
}
</style>


