<!--  -->
<template>
    <div style="width:1920px;height:932px;">
        <div class="nav-header">
            <div class="nav-header__title" @click="Home">
                <span style="font-size:40px; font-style: italic;">未来之路</span><br>
                <span style="font-size:18px;margin-left:35%">一站式校园助手</span>
            </div>
            <div class="nav-header__avatar" @click="UserMenu">
                <div style="width: 64px;height: 64px;">
                    <el-avatar :size="Avatar.size" :src="Avatar.url"></el-avatar>
                </div>
            </div>
        </div>
        <div class="nav-body">
            <sys ref="SysSetting" />
            <routeplan ref="RoutePlan" @draw="drawLine" @close="removeAll" />
            <runninghelper ref="RunningHelper" @drawrun="drawRunning" @close="removeAll" />
            <about ref="About" />
            <datelog ref="DateLog" @drawdate="drawDate" @close="removeAll" />
            <div id="antvMap"></div>
            <div style="position: absolute; z-index: 10;">
                <el-menu default-active="" class="el-menu-vertical-demo" @select="MenuSelect" :collapse="isCollapse">
                    <el-submenu index="1">
                        <template slot="title">
                            <i class="el-icon-location"></i>
                            <span slot="title">校园</span>
                        </template>
                        <el-menu-item-group>
                            <span slot="title">导航</span>
                            <el-menu-item index="1-1">路径指引</el-menu-item>
                        </el-menu-item-group>
                        <el-menu-item-group title="运动">
                            <el-menu-item index="1-2">乐跑助手</el-menu-item>
                        </el-menu-item-group>
                        <el-menu-item-group title="规划">
                            <el-menu-item index="1-3">日程规划</el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                    <el-submenu index="4">
                        <template slot="title">
                            <i class="el-icon-setting"></i>
                            <span slot="title">系统</span>
                        </template>
                        <el-menu-item-group>
                            <span slot="title">基础</span>
                            <el-menu-item index="4-1">外观</el-menu-item>
                            <el-menu-item index="4-2">快捷键</el-menu-item>
                        </el-menu-item-group>
                        <el-menu-item-group title="功能">
                            <el-menu-item index="4-3">默认项</el-menu-item>
                            <el-menu-item index="4-4">自定义</el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                    <el-menu-item index="5">
                        <i class="el-icon-info"></i>
                        <span slot="title">关于</span>
                    </el-menu-item>
                </el-menu>
            </div>
        </div>
    </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import { AntvMap } from '../../static/Plugins/Script/antvMap.js'
import About from '../components/About.vue'
import Sys from '../components/Sys.vue'
import Runninghelper from '../components/RunningHelper.vue'
import Routeplan from '../components/RoutePlan.vue'
import Datelog from '../components/DateLog.vue'

export default {
    //import引入的组件需要注入到对象中才能使用
    components: {
        Sys,
        About,
        Runninghelper,
        Routeplan,
        Datelog
    },
    data() {
    //这里存放数据
        return {
            Avatar:{
                size:64,
                url:'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png'
            },

            isCollapse:false,

            SysOPtions:{
                activeName:'',
                isAccordion:true
            },

            AntvMapInstance:null,

            currentLayerName:null,
            polygonlayerName:null,
        };
    },
    //监听属性 类似于data概念
    computed: {},
    //监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        init(){
            this.AntvMapInstance = new AntvMap( 'antvMap' );
            this.AntvMapInstance.loadMap();
        },

        // 主菜单栏弹出
        Home(){
            this.isCollapse = !this.isCollapse;
        },

        // 用户菜单栏弹出
        UserMenu(){

        },

        MenuSelect(index){
            let options = '';
            switch(index){
                case '1-1':
                    options = 'RoutePlan'
                    break;
                case '1-2':
                    options = 'RunningHelper'
                    break;
                case '1-3':
                    options = 'DateLog'
                    break;
                case '4-3':
                    options = 'SysSetting'
                    break;
                case '5':
                    options = 'About'
                    break;
                default:
                    this.$message('功能建设中，敬请期待~');
                    return;
            }
            this.$refs[options].onShow();
        },

        // 路径规划绘制部分
        drawLine(data){
            console.log(data)
            // 清除历史图层
            this.removeAll();
            // 加载起点跟终点
            this.AntvMapInstance.addMarker(data.point);
            this.currentLayerName = [data.point.name];

            // 如果选择的室内停车场，就画室内停车场位置
            if('polygon' in data){
                this.AntvMapInstance.addPolygon(data.polygon);
                this.currentLayerName.push(data.polygon.name);
                this.polygonlayerName = data.polygon.name;
            }
            // 否则去掉之前画的室内停车场
            else{
                if(this.polygonlayerName != null){
                    this.AntvMapInstance.removeLayer(this.polygonlayerName)
                }
            }

            // 添加路径图层
            if(data.line.name == 'walk' || data.line.name == 'noparking'){
                this.AntvMapInstance.addLine(data.line);
                this.currentLayerName.push(data.line.name);
            }else{
                // 这里分别画出车行跟人行路径
                this.AntvMapInstance.addLine(data.line.one);
                this.AntvMapInstance.addLine(data.line.two);
                this.currentLayerName.push(data.line.one.name);
                this.currentLayerName.push(data.line.two.name);
            }
        },

        // 乐跑助手绘制部分
        drawRunning(data){
            // 清除历史图层
            this.removeAll();

            // 画路径跟点
            this.AntvMapInstance.addLine(data.line);
            this.AntvMapInstance.addMarker(data.point);
            this.currentLayerName = [data.line.name,data.point.name];
        },     

        drawDate(data){
            // 清除历史图层
            this.removeAll();

            this.AntvMapInstance.addLine(data);
            this.currentLayerName = data.name;
        },

        removeAll(){
            if(this.currentLayerName != null){
                if(typeof(this.currentLayerName) == 'string'){
                    this.AntvMapInstance.removeLayer(this.currentLayerName)
                }else{
                    for( let i in this.currentLayerName){
                        this.AntvMapInstance.removeLayer(this.currentLayerName[i])
                    }
                }
            }
        }
    },
    //生命周期 - 创建完成（可以访问当前this实例）
    created() {

    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {
        this.init();
    },
    beforeCreate() {}, //生命周期 - 创建之前
    beforeMount() {}, //生命周期 - 挂载之前
    beforeUpdate() {}, //生命周期 - 更新之前
    updated() {}, //生命周期 - 更新之后
    beforeDestroy() {}, //生命周期 - 销毁之前
    destroyed() {}, //生命周期 - 销毁完成
    activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped>
    .nav-body{
        position: relative;
        width: 100%;
        height: 91.4%;
    }
    #antvMap{
        position: absolute;
        width: 100%;
        height: 100%;
    }

    .nav-header{
        position: relative;
        width:100%;
        height: 8.6%;
        background-color: #409EFF;
        z-index: 10;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .nav-header__title{
        display: inline-block;
        width: 200px;
        margin-left: -88%;
    }
    .nav-header__title span{
        color: white;
        font-family: 'common-Regular';
    }
    .nav-header__title span,
    .nav-header__avatar:hover{
        cursor: pointer;
    }

    .nav-header__avatar{
        display: inline-block;
        position: absolute;
        right: 1%;
    }

    .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 200px;
        min-height: 851px;
    }

</style>

<style>
    .el-collapse-item__header{
        font-size:18px !important;
        padding-left: 10%;
    }
    .el-drawer__header span{
        font-size: 20px;
    }
</style>


